.scrollingContainer {
    width: 100%;       /* Full width of the container */
    overflow: hidden;  /* Hide any overflow text */
  }
  
  .scrollingText {
    display: inline-block;
    font-size: 24px; 
    position: relative;
    white-space: nowrap; 
    text-align: center;
    width: 100%;
  }
  