.header {
    position: relative;
    width: 100%;
    height: 30vh;
    /* Adjust the height as needed */
    background-image: url('https://images.unsplash.com/photo-1541888946425-d81bb19240f5?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    color: #fff;
    background-position: center 70%;
  
  }
  
  
  .title {
    font-family: 'Orbitron', sans-serif;
    font-size: 3rem;
    margin-bottom: 20px;
    text-align: left !important;
    padding-left: 40px;
  }
  
  h1 {
    text-align: left;
  }
  
  .cta {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    background: transparent;
    color: var(--white);
    font-weight: 700;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    margin: 0.5rem;
    transition: color 0.3s, border-color 0.3s;
    padding-left: 30px ;
  }
  
  .cta:hover {
    color: var(--blue);
    border-color: var(--blue);
  }
  
  .cta .chevrons {
    display: flex;
    gap: 0.2rem;
    margin-left: 0.5rem;
    /* Space between text and chevrons */
    transition: color 0.3s;
    /* Smooth color transition */
  }
  
  .cta:hover .chevrons i {
    color: var(--blue);
    /* Chevron color on hover */
  }
  
  .cta .chevrons i {
    color: var(--white);
    /* Default chevron color */
  }
  
  @media (max-width: 768px) {
    .header {
      height: 30vh;
      /* Adjust for smaller screens */
    }
  
    .title {
      font-size: 2rem;
    }
  
    .cta {
      font-size: 1rem;
      padding: 8px 16px;
    }
  }
  