:root {
  --sec: "Space Grotesk";
  --principal: Orbitron;
  --bg: #f3f8fa;
  --white: #EAEAE5;
  --blue: #2877CE;
  --grey: #7D8690;
}

body {
  font-family: var(--sec), sans-serif;
  background-color: var(--bg);
  padding: 0px;
  margin: 0px;
  overflow: scroll;
}

.container {
  min-height: 70vh;
  overflow-x: hidden;
}


.navLink {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
}

.learnMore {
  width: 13rem;

  height: auto;
}

.circle {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: relative;
  display: block;
  margin: 0;
  width: 2rem;

  height: 2rem;

  background: var(--blue);
  border-radius: 1rem;

}

.icon {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  background: var(--white);
}

.arrow {
  left: 0.5rem;
  width: 0.875rem;
  height: 0.1rem;
  background: none;
}

.arrow::before {
  position: absolute;
  content: '';
  top: -0.2rem;
  right: 0.05rem;
  width: 0.5rem;
  height: 0.5rem;
  border-top: 0.1rem solid #fff;
  border-right: 0.1rem solid #fff;
  transform: rotate(45deg);
}

.buttonText {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0.5rem 0;
  margin: 0 0 0 1.25rem;
  color: var(--blue);
  font-weight: 700;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
}

.navLink:hover .circle {
  width: 100%;
}

.navLink:hover .icon.arrow {
  background: var(--white);
  transform: translate(1rem, 0);
}

.navLink:hover .buttonText {
  color: var(--white);
}
